.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71,71,71);
    margin:20px;
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background: white;
    min-width: 40%;
}
.image{
    width:300px;
    height:300px;
    border-radius: 50%;
}